@import '../variables.scss';

.header {
  border-bottom: 1px solid $gray-line;
  padding: 40px 0;
  @media (max-width: 645px){
    padding: 30px 0;
  }

  .container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    @media (max-width: 645px){
      .button--cart{
        padding: 6px 14px;
        min-width: unset;
      }
      .button__delimiter, .totalPrice{
        display: none;
      }
    }
  }

  &__logo {
    display: flex;
    align-items: center;

    svg {
      margin-right: 15px;
    }

    h1 {
      color: #181818;
      font-size: 24px;
      letter-spacing: 1%;
      text-transform: uppercase;
      font-weight: 800;
    }

    p {
      color: #7b7b7b;
      font-size: 16px;
    }

    @media (max-width: 645px){
      h1{font-size: 20px;}
      p{
        font-size: 10px;
        display: none;
      }
    }
  }
}
